/* =================================== */
/* =================================== */

/* Members and projects */
.dashboard-Container .membersAndProjectsContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin-bottom: 25px;
}


/* ******************************** */
/* ******************************** */
/* ******************************** */
/* Members */
.dashboard-Container .members {
    width: 100%;
    background-color: var(--white);
    border-radius: 10px;
    padding: 20px 10px;
}

.dashboard-Container .members>div>div:nth-child(2) {
    overflow-x: auto;
}

.dashboard-Container .members>div>div:nth-child(2)>div {
    width: 100%;
    min-width: 500px;
}

.dashboard-Container table {
    min-width: 100%;
    border-collapse: collapse;
}

/* Table column groupe */
.dashboard-Container .members table colgroup col:nth-child(1) {
    width: 20%;
}

.dashboard-Container .members table colgroup col:nth-child(2) {
    width: 40%;
}

.dashboard-Container .members table colgroup col:nth-child(3) {
    width: 20%;
}

.dashboard-Container .members table colgroup col:nth-child(4) {
    width: 20%;
}

/* Table head */
.dashboard-Container table th {
    background-color: var(--black);
    word-wrap: normal;
    padding: 20px 0px 20px 5px;
}

.dashboard-Container table th>div {
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: left;
    margin-right: 5px;
}

.dashboard-Container table th>div svg {
    width: 15px;
    height: 100%;
}


/* data */
.dashboard-Container table tbody tr:last-child td {
    border-bottom: none;
}

.dashboard-Container table img {
    width: 40px;
    border: 2px solid var(--main-clr);
    border-radius: 50%;
    cursor: pointer;
}

.dashboard-Container .members .numOfProjects {
    color: var(--gray-dark);
    font-weight: 600;
}

.dashboard-Container .members .role {
    width: fit-content;
    color: var(--gray);
    border: 2px solid var(--border-color);
    border-radius: 5px;
    padding: 5px 10px;
}

/* Pagination */
.dashboard-Container tfoot td>div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}


/* ************************ */
/* ************************ */
/* ************************ */
/* projects */
.dashboard-Container .projects {
    width: 100%;
    background-color: var(--white);
    border-radius: 10px;
    padding: 20px 10px;
}

.dashboard-Container .projects>div>div:nth-child(2) {
    overflow-x: auto;
}

.dashboard-Container .projects>div>div:nth-child(2)>div {
    width: 100%;
    min-width: 600px;
}


/* Table column groupe */
.dashboard-Container .projects table colgroup col:nth-child(1) {
    width: 40%;
}

.dashboard-Container .projects table colgroup col:nth-child(2) {
    width: 15%;
}

.dashboard-Container .projects table colgroup col:nth-child(3) {
    width: 15%;
}

.dashboard-Container .projects table colgroup col:nth-child(4) {
    width: 20%;
}

.dashboard-Container .projects table colgroup col:nth-child(5) {
    width: 10%;
}

.dashboard-Container .projects table td:not(tfoot td) {
    border-bottom: 1px solid var(--border-color);
    padding: 15px 0px 15px 5px;
}

/* name & pm */
.dashboard-Container .projects .name {
    color: var(--blue);
    cursor: pointer;
    margin-bottom: 5px;
}

.dashboard-Container .projects .pm {
    color: var(--gray)
}

/* team */
.dashboard-Container .projects .team {
    display: flex;
    align-items: center;
}

.dashboard-Container .projects .team img {
    width: 35px;
    border: 2px solid var(--main-clr);
    border-radius: 50%;
    cursor: pointer;
    transition: .25s;
}

.dashboard-Container .projects .team a:not(:nth-child(1)) {
    margin-left: -12px;
}

.dashboard-Container .projects .team img:hover {
    transform: translateY(-5px);
}

/* dates */
.dashboard-Container .projects .startDate,
.dashboard-Container .projects .endDate {
    width: fit-content;
    font-size: .8rem;
    border: 2px solid;
    border-radius: 5px;
    padding: 5px;
}

.dashboard-Container .projects .startDate {
    color: var(--success);
}

.dashboard-Container .projects .endDate {
    color: var(--danger);
}

/* tickets */
.dashboard-Container .projects .numOfTicket {
    color: var(--gray-dark);
    font-weight: 600;
}


/* Table foot */
.dashboard-Container table td:not(tfoot td) {
    border-bottom: 1px solid var(--border-color);
    padding: 10px 0px 5px 5px;
}