.navbarContainer {
    width: 100%;
    max-width: 250px;
    height: calc(100vh - 82px);
    background-color: var(--white);
    border-right: 1px solid var(--border-color);
    border-top: 1px solid var(--border-color);
    overflow: visible;
    overflow-y: scroll;
    position: sticky;
    top: 83px;
    transition: .5s;
    z-index: 10;
}

.navbarContainer::-webkit-scrollbar {
    display: none;
}


.navbarContainer .info {
    display: flex;
    justify-content: flex-start;
    align-self: flex-start;
    gap: 10px;
    position: relative;
    padding: 15px 0px 25px 25px;
}

.navbarContainer .info>div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.navbarContainer .info img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.navbarContainer .info .name {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 800;
    font-size: 1.1rem;
    text-transform: capitalize;
    margin: 5px 0;
}

.navbarContainer .info .role {
    color: var(--teal);
    display: flex;
    justify-content: center;
    font-size: .7rem;
    font-weight: 600;
    text-transform: uppercase;
    border: 2px solid var(--teal);
    border-radius: 5px;
    padding: 3px 10px;
}

.navbarContainer .info .arrow-down * {
    display: flex;
    font-size: .9rem;
    fill: var(--black);
}

/* DropDown */
.navbarContainer .dropDown {
    width: 150px;
    background-color: var(--gray-dark);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    position: absolute;
    right: 60px;
    top: 55px;
    box-shadow: var(--shadow);
    transform-origin: top;
    transition: .25s;
    transform: scaleY(0);
    z-index: 10;
    padding: 15px 5px;
}

.navbarContainer .dropDown li {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    color: var(--white);
    font-weight: 600;
    cursor: pointer;
}

.navbarContainer .dropDown li svg {
    font-size: .8rem;
}

.navbarContainer .dropDown li:nth-child(2) {
    border-bottom: 2px solid var(--gray);
    padding: 15px 0px;
    margin-bottom: 15px;
}

.navbarContainer .dropDown li:hover {
    text-decoration: 2px underline;
}

/* navigation */
.navigationContainer .head {
    display: flex;
    align-items: center;
    position: relative;
    margin: 0 0 10px 0;
}

.navigationContainer .head h3 {
    width: 96px;
    color: var(--main-clr);
    border-radius: 5px 5px 0px 0px;
    cursor: pointer;
    user-select: none;
    padding: 10px 20px;
}


.navigationContainer .head i {
    width: 50px;
    transition: border-bottom 0s;
    padding: 10px 15px 8px 15px;
    margin-left: -1px;
}

.navigationContainer .head svg {
    transform: scale(.7);
}

.navigationContainer .head i:hover {
    background-color: var(--main-clr-hover);
    border: 1px solid var(--border-color);
    border-radius: 5px 5px 0 0;
    opacity: .5;
}

.navigationContainer .head i:hover svg path {
    fill: var(--main-clr)
}

.navigationContainer .head .filler {
    height: 100%;
    width: calc(100% - 145px);
    position: absolute;
    right: 0;
    background-color: transparent;
    border-bottom: 1px solid var(--border-color);
}

/* List */
.navigationContainer .list {
    display: flex;
    flex-direction: column;
}

.navigationContainer .list li {
    display: flex;
    align-items: center;
    gap: 15px;
    border-left: 5px solid var(--main-clr);
    cursor: pointer;
    padding: 15px 10px 15px 15px;
    margin-bottom: 10px;
}

.navigationContainer .list li:nth-child(3),
.navigationContainer .list li:nth-child(5),
.navigationContainer .list li:nth-child(7),
.navigationContainer .list li:nth-child(9) {
    margin-bottom: 0px;
}

.navigationContainer .list li:hover {
    background-color: var(--main-clr-hover);
}

.navigationContainer .list li .arrow {
    transform-origin: center;
    transform: rotate(90deg);
    transition: .5s;
    margin-left: auto;
}

.navigationContainer .list li i svg {
    font-size: .9rem;
}

.navigationContainer .list li .arrow path {
    fill: var(--gray);
}

.navigationContainer .list li i svg path {
    fill: var(--main-clr);
}

/* options */
.navigationContainer .options {
    max-height: 0px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: hidden;
    transition: max-height .5s ease-in-out;
    padding: 0px 0 10px 25px;
}

.navigationContainer .options span:nth-child(1) {
    margin-top: 15px;
}

.navigationContainer .options span {
    display: flex;
    align-items: center;
    gap: 25px;
}

.navigationContainer .options span p {
    color: var(--gray);
    cursor: pointer;
}

.navigationContainer .options span p:hover {
    text-decoration: 2px underline;
}

.navigationContainer .options span svg {
    font-size: .5rem;
}

.navigationContainer .options span svg path {
    fill: var(--black);
}

/* skins */
.navigationContainer .skins {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 10px 0 0 15px;
}

.navigationContainer .skins h4 {
    margin-bottom: 5px;
}

.navigationContainer .skins span {
    display: flex;
    align-items: center;
    gap: 5px;
    text-transform: capitalize;
    cursor: pointer;
}

.navigationContainer .skins span .dot {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigationContainer .skins span i svg path {
    fill: inherit;
}

.navigationContainer .skins span i:hover {
    opacity: 1;
}

.navigationContainer .skins span .check {
    position: absolute;

}

.navigationContainer .skins span .check svg {
    fill: var(--white);
    font-size: .8rem;
}


/* *************************** */
/* *************************** */
/* *************************** */
/* media queries */
@media screen and (max-width:1100px) {
    .navbarContainer {
        position: absolute;
        top: 88px;
        transform: translateX(-100%);
        box-shadow: var(--shadow);
    }
}


/* ************************************ */
/* ************************************ */
/* ************************************ */
/* Javascript classes */
.navigationContainer .list .clicked {
    background-color: var(--main-clr-hover);
}

.navigationContainer .list>li .clickedArrow {
    transform: rotate(0deg);
}

.navigationContainer .openDropDown {
    max-height: 324px;
}

.navigationContainer .menuOnHead h3 {
    border-top: 1px solid var(--border-color);
    border-right: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
    pointer-events: none;
}

.navigationContainer .menuOnHead i {
    border-bottom: 1px solid var(--border-color);
    border-top: 1px solid transparent;
    border-right: 1px solid transparent;
    border-left: 1px solid transparent;
}

.navigationContainer .menuOffHead h3 {
    color: var(--black);
    border-top: 1px solid transparent;
    border-bottom: 1px solid var(--border-color);
    border-right: 1px solid transparent;
    border-left: 1px solid transparent;
}

.navigationContainer .menuOffHead h3:hover {
    color: var(--main-clr);
    border-top: 1px solid var(--border-color);
    border-right: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
    opacity: .5;
}

.navigationContainer .menuOffHead i {
    border-top: 1px solid var(--border-color);
    border-bottom: 1px solid transparent;
    border-right: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
    border-radius: 5px 5px 0 0;
    pointer-events: none;
}

.navigationContainer .menuOffHead i path {
    fill: var(--main-clr);
}

.showNavbarContainer {
    transform: translateX(0%);
}

.navbarContainer .showMiniDropDown {
    transform: scaleY(1);
}